.home > div {
  min-height: calc(100vh - 85px);
  position: relative;

  h2 {
    margin: 2rem auto;
    border-bottom: 1px solid $teal;
    padding-bottom: 0.5rem;
    font-size: 2rem;
  }
}

#landing {
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
  }

  .scroll {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);

    a {
      display: block;
      font-size: 2.5rem;
    }
  }
}

#about {
  .profile {
    border-radius: 50%;
    max-width: 100%;
    width: 225px;
    height: 225px;
    margin: 2rem auto;
    border: 2px solid $white;
    background-image: url('../img/profile.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  h3 {
    margin-bottom: 0.5rem;
  }

  .icon {
    margin-right: 0.5rem;
    font-size: 1.25rem;
  }

  li:not(:last-child) {
    margin-bottom: 0.5rem;
  }

  @media (max-width: 766px) {
    .col {
      width: 100%;
    }
  }

  @media (min-width: 767px) {
    position: relative;

    > .row {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
    }

    .nine.col {
      padding-left: 3rem;
    }
  }
}

#resume {
  h3 {
    padding-bottom: 0.5rem;
    border-bottom: 1px solid $orange;
    width: 100%;
    font-size: 1.5rem;
  }

  h4 {
    margin: 0.9rem 0 0.9rem;
    font-size: 1.25rem;
  }

  .first.col {
    padding: 0.5rem 0.5rem 0 0;
    width: 115px;
  }

  .second.col {
    width: calc(100% - 115px);
    border-left: 1px solid $orange;
    padding: 0.5rem 0 0 0.5rem;
  }

  span {
    color: $white;
  }

  p {
    position: relative;
  }

  .month {
    position: absolute;
    top: -1rem;
    color: $lightgray;

    &.from {
      left: 0;
    }

    &.to {
      margin-left: 3px;
    }
  }

  .year {
    float: left;

    &.to {
      margin-left: 3px;
    }
  }
}

#portfolio {
  .list {
    display: flex;
    flex-wrap: wrap;
  }
}

.project {
  flex: 0 0 auto;
  width: 100%;
  padding: 0;
  margin-bottom: 2rem;
  overflow-x: hidden;

  @media (min-width: 767px) {
    width: 50%;

    &:nth-child(odd) {
      padding-right: 1rem;
    }

    &:nth-child(even) {
      padding-left: 1rem;
    }

    &:nth-child(2n+3) {
      padding-top: 1rem;
    }

    &:nth-child(2n+4) {
      padding-top: 1rem;
    }
  }
}

.screenshot {
  height: 200px;
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  margin-bottom: 1rem;

  a {
    display: block;
    height: 100%;
    width: 100%;
  }
}

.info {
  display: flex;

  span {
    flex: 1;

    &:last-child {
      text-align: right;
    }
  }
}

.tags {
  font-style: italic;
  font-size: 0.85rem;
}

.descrip {
  color: $white;
}
